
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from '../../../../config/api.config';
import { PageableDto, PageOptionsDto } from './../../interfaces/others/pageable.dto';
import { PageUtils } from '../../utils/PageUtils';
import { ChamadoDto, CreateChamadoDto, PatchStatusChamadoDto, SearchChamadoDto, UpdateChamadoDto } from '../../interfaces/chamados/chamado.dto';
import { ArquivoGeralDto } from '../../interfaces/arquivos/arquivo-geral.dto';

@Injectable({
  providedIn: 'root'
})
export class ChamadoService {

  constructor(
    private http: HttpClient,
  ) { }

  list(filterDto?: SearchChamadoDto, pageOptions = new PageOptionsDto()): Observable<PageableDto<ChamadoDto>> {
    let params = PageUtils.getPageOptionsParams(pageOptions);

    if (filterDto) {
      if (filterDto.encerrador_id) {
        params = params.append('encerrador_id', filterDto.encerrador_id);
      }
      if (filterDto.executor_id) {
        params = params.append('executor_id', filterDto.executor_id);
      }
      if (filterDto.searched_chamado) {
        params = params.append('searched_chamado', filterDto.searched_chamado);
      }
      if (filterDto.solicitante_id) {
        params = params.append('solicitante_id', filterDto.solicitante_id);
      }
      if (filterDto.status) {
        params = params.append('status', filterDto.status);
      }
      if (filterDto.tipo) {
        params = params.append('tipo', filterDto.tipo);
      }

      if (filterDto.min_data_closed) {
        params = params.append('min_data_closed', filterDto.min_data_closed.toDateString());
      }
      if (filterDto.min_data_created) {
        params = params.append('min_data_created', filterDto.min_data_created.toDateString());
      }
      if (filterDto.max_data_closed) {
        params = params.append('max_data_closed', filterDto.max_data_closed.toDateString());
      }
      if (filterDto.max_data_created) {
        params = params.append('max_data_created', filterDto.max_data_created.toDateString());
      }
    }

    return this.http.get<PageableDto<ChamadoDto>>(`${API_CONFIG.baseURL}/chamados`, {
      responseType: 'json',
      params,
    });
  }

  create(newChamado: CreateChamadoDto): Observable<ChamadoDto> {
    return this.http.post<ChamadoDto>(
      `${API_CONFIG.baseURL}/chamados`,
      newChamado,
      {
        responseType: 'json',
      });
  }

  update(chamado_id: string, updatedChamado: UpdateChamadoDto): Observable<ChamadoDto> {
    return this.http.put<ChamadoDto>(
      `${API_CONFIG.baseURL}/chamados/${chamado_id}`,
      updatedChamado,
      {
        responseType: 'json',
      });
  }

  updateStatus(chamado_id: string, updatedChamado: PatchStatusChamadoDto): Observable<ChamadoDto> {
    return this.http.patch<ChamadoDto>(
      `${API_CONFIG.baseURL}/chamados/${chamado_id}/status`,
      updatedChamado,
      {
        responseType: 'json',
      });
  }

  createAnexo(anexo: File): Observable<ArquivoGeralDto> {
    const formData = new FormData();

    formData.append('file', anexo);
    return this.http.post<ArquivoGeralDto>(
      `${API_CONFIG.baseURL}/chamados/anexos/fotos`,
      formData,
      {
        responseType: 'json',
      });
  }

  associarAnexo(id: string, anexo: File): Observable<ChamadoDto> {
    const formData = new FormData();

    formData.append('file', anexo);
    return this.http.patch<ChamadoDto>(
      `${API_CONFIG.baseURL}/chamados/${id}/anexos/fotos`,
      formData,
      {
        responseType: 'json',
      });
  }
}
