import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from '../../../../config/api.config';
import { CreateEncomendaDto, EncomendaDto, FinalizarEncomendaDto, SearchEncomendaDto, UpdateEncomendaDto } from '../../interfaces/encomendas/encomenda.dto';
import { PageableDto, PageOptionsDto } from '../../interfaces/others/pageable.dto';
import { PageUtils } from '../../utils/PageUtils';
import { ArquivoGeralDto } from '../../interfaces/arquivos/arquivo-geral.dto';

@Injectable({
  providedIn: 'root'
})
export class EncomendasService {

  constructor(
    private http: HttpClient,
  ) { }

  list(filterDto?: SearchEncomendaDto, pageOptions = new PageOptionsDto()): Observable<PageableDto<EncomendaDto>> {
    let params = PageUtils.getPageOptionsParams(pageOptions);

    if (filterDto) {
      if (filterDto.codigo_rastreio) {
        params = params.append('codigo_rastreio', filterDto.codigo_rastreio);
      }
      if (filterDto.nf_rastreio) {
        params = params.append('nf_rastreio', filterDto.nf_rastreio);
      }
      if (filterDto.nome_remetente) {
        params = params.append('nome_remetente', filterDto.nome_remetente);
      }
      if (filterDto.recebedor_final_id) {
        params = params.append('recebedor_final_id', filterDto.recebedor_final_id);
      }
      if (filterDto.recebedor_inicial_id) {
        params = params.append('recebedor_inicial_id', filterDto.recebedor_inicial_id);
      }
      if (filterDto.searched_encomenda) {
        params = params.append('searched_encomenda', filterDto.searched_encomenda);
      }
      if (filterDto.status) {
        params = params.append('status', filterDto.status);
      }
      if (filterDto.user_associado_id) {
        params = params.append('user_associado_id', filterDto.user_associado_id);
      }
    }

    return this.http.get<PageableDto<EncomendaDto>>(`${API_CONFIG.baseURL}/encomendas`, {
      responseType: 'json',
      params,
    });
  }

  create(newEncomenda: CreateEncomendaDto): Observable<EncomendaDto> {
    if (newEncomenda.codigo_rastreio == null) {
      newEncomenda.codigo_rastreio = undefined;
    }
    if (newEncomenda.nf_rastreio == null) {
      newEncomenda.nf_rastreio = undefined;
    }

    return this.http.post<EncomendaDto>(
      `${API_CONFIG.baseURL}/encomendas`,
      newEncomenda,
      {
        responseType: 'json',
      });
  }

  update(encomenda_id: string, updatedEncomenda: UpdateEncomendaDto): Observable<EncomendaDto> {
    if (updatedEncomenda.codigo_rastreio == null) {
      updatedEncomenda.codigo_rastreio = undefined;
    }
    if (updatedEncomenda.nf_rastreio == null) {
      updatedEncomenda.nf_rastreio = undefined;
    }

    return this.http.put<EncomendaDto>(
      `${API_CONFIG.baseURL}/encomendas/${encomenda_id}`,
      updatedEncomenda,
      {
        responseType: 'json',
      });
  }


  finalizar(encomenda_id: string, updatedEncomenda: FinalizarEncomendaDto): Observable<EncomendaDto> {
    return this.http.put<EncomendaDto>(
      `${API_CONFIG.baseURL}/encomendas/${encomenda_id}/finalizar`,
      updatedEncomenda,
      {
        responseType: 'json',
      });
  }

  createComprovante(anexo: File): Observable<ArquivoGeralDto> {
    const formData = new FormData();

    formData.append('file', anexo);
    return this.http.post<ArquivoGeralDto>(
      `${API_CONFIG.baseURL}/encomendas/anexos/comprovantes`,
      formData,
      {
        responseType: 'json',
      });
  }

  associarComprovante(id: string, anexo: File): Observable<EncomendaDto> {
    const formData = new FormData();

    formData.append('file', anexo);
    return this.http.patch<EncomendaDto>(
      `${API_CONFIG.baseURL}/encomendas/${id}/anexos/comprovantes`,
      formData,
      {
        responseType: 'json',
      });
  }
}
